


<template>
  <HelloWorld msg="Welcome barans vue experiment"/>
  <p class="card-text">Name : {{baransinfo}}</p>

  <table class="table table-striped">
    <thead>
      <tr>
        <th scope="col">Rank</th>
        <th scope="col">Name</th>
        <th scope="col">Score</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(item, index) in leaderboard" :key="index">
        <th scope="row">{{index+1}}</th>
        <td>{{item}}</td>
      </tr>
    </tbody>
  </table>




  <button v-on:click="incrementScore()">increment my number</button>


  <form v-on:submit.prevent="setName(name)">
    <input type="text" v-model="name">
    <input type="submit" value="Submit">
  </form>











</template>

<script>
import HelloWorld from './components/HelloWorld.vue'


//import axios from 'axios'
//import {PlayFabClient} from 'playfab-sdk/Scripts/PlayFab/PlayFabClient'
import {PlayFab, PlayFabClient} from "playfab-sdk";
PlayFab.settings.titleId = "67A0F";





export default {
  name: 'App',
  mounted(){
    // get a random number
    let myId = Math.random();
    console.log("called login");
    this.login(myId, () =>{
      console.log("logged in");
      this.setName(myId, () => {
        console.log("set name");
      });
    });

    this.baransinfo = "mounted";

    // run action every second
    let i = 0;
    setInterval(() => {
      this.baransinfo = "setInterval + " + i;
      i++;
    }, 1000);



  },
  data(){
    return {
      baransinfo: "initial info",
      items: [
          "item 1",
          "item 2",
          "item 3",
          "item 4",
      ],
      baransButtonName: "bar",
      leaderboard: [],


      login: function(myCustomId, callback){
        console.log("login really got called");
        PlayFabClient.LoginWithCustomID({
          CreateAccount: true,
          CustomId: myCustomId
        },(error, result) => {
          console.log("login really returned");
          if(result){
            console.log(result);
            callback();
          }
          if(error){
            console.log(error);
          }
        });
      },
      setName: function(name, callback){
        console.log("setName really got called with paramenter: " + name);
        PlayFabClient.UpdateUserTitleDisplayName({
          DisplayName: name
        }, (error, result) => {
          if(result && callback) callback();
        });
      },
      incrementClick: function(callback){
        PlayFabClient.ExecuteCloudScript({
          FunctionName: "incrementClicks"
        }, (error, result) => {
          if(result) callback();
        });
      },
      getClickLeaderboard: function(callback){
        PlayFabClient.GetLeaderboard({
          StartPosition: 0,
          MaxResultsCount: 50,
          StatisticName: "clicks"
        },(error, result) => {
          if (result) {
            callback(result.data.Leaderboard);
          }
        })
      },
      incrementScore: function() {
        this.incrementClick(() => {
          this.getClickLeaderboard((response) => {
            console.log("getClickLeaderboard");
            console.log(response);

            let processedLeaderboard = [];
            response.forEach(item => {
              this.leaderboard = [];
              console.log("item: " + item.DisplayName);
              processedLeaderboard.push(item.DisplayName + " : " + item.StatValue);
            });
            this.leaderboard = processedLeaderboard;
          });
        });
      }
    }
  },
  components: {
    HelloWorld
  }
}



</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>

